/** UI **/
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  z-index: 0;
}

.header {
  background-color: #0f3973;
  padding: 1rem 0 0 0;
  position: relative;
}
.header .video-container {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  z-index: 0;
  transition: clip-path 0.7s ease;
  clip-path: inset(0 0 0 0); /* Initially fully visible */
  display: flex;
  align-items: end;
  justify-content: center;
}
.header .video-container video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-height: 100%;
  width: 103%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  filter: brightness(60%);
}
.header .video-container .titulo-header {
  position: relative;
  z-index: 1;
  text-transform: uppercase;
}
.header .video-container .titulo-header h1 {
  padding: 0 1rem 4rem 1rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 80rem;
  font-size: 2.3rem;
}
@media (min-width: 768px) {
  .header .video-container .titulo-header h1 {
    font-weight: 900;
    padding-bottom: 0;
    padding: 0 0 10rem 2rem;
  }
}
@media (min-width: 768px) {
  .header .video-container .titulo-header h1 {
    padding-left: 0;
    font-size: 3.8rem;
  }
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-color: transparent;
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  height: 100%;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  z-index: 1;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .contenido-header {
    padding-bottom: 1rem;
  }
}

.footer {
  padding: 2.5rem;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}

.barra {
  display: flex;
  flex-direction: column;
}
.barra img {
  width: 20rem;
}
.barra .active {
  color: rgb(239, 62, 66);
}
.barra .barra-interna {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
  .barra.fixed {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 0;
    width: 100%;
    background-color: rgba(68, 6, 182, 0.705);
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
    padding: 0.5rem;
    padding-right: 2rem;
  }
  .barra.fixed img {
    width: 15rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  font-weight: 700;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
    font-size: 1.5rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: rgb(239, 62, 66);
  }
}

.mobile-menu {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.clip {
  /* Push the main content below the header initially */
}

