.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
  justify-content: space-between;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  position: relative;
  justify-content: center;
}
.iconos-caracteristicas li::after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 90%; /* Position above the element */
  padding: 5px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  white-space: nowrap;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
  font-size: 12px;
  pointer-events: none; /* Prevents the tooltip from being interactive */
  z-index: 4;
}
.iconos-caracteristicas li:hover::after {
  visibility: visible;
  opacity: 1;
}
.iconos-caracteristicas li.prescription::after {
  content: "Prescriptions available";
}
.iconos-caracteristicas li.xray::after {
  content: "X-ray available";
}
.iconos-caracteristicas li.airevac::after {
  content: "Air evac available";
}
.iconos-caracteristicas li img {
  flex: 0 0 2rem;
  margin-right: 3rem;
  width: 6rem;
  cursor: pointer;
  position: relative;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.contenedor-clinicas {
